import React from "react";
import { Link } from "react-router-dom";
import { useState } from 'react';
import "./Navbar.scss";

const Navbar = ({ isLoggedIn }) => {
  const devpartnersIMG = require("../../assets/img/logos/devpartners-logo.png");
  const [isShowRightNav, setSsShowRightNav] = useState(false);

  const navToggle = () => { 
    if (isShowRightNav) {
      setSsShowRightNav(false);
      return;
    }
    setSsShowRightNav(true);
  };

  if (!isLoggedIn) {
    return (
      <div className="navbar-container">
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
          <div className="container mobile-container-nav">
            <Link className="navbar-brand" to="/">
              <img
                className="devpartnersBrand"
                src={devpartnersIMG}
                alt="devpartners_brand"
              />
            </Link>
            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarNavDropdown"
              aria-controls="navbarNavDropdown"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <button className="mobileMenuButton" onClick={() => { navToggle() }}>
              <svg width="25px" height="18px" viewBox="0 0 25 18" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                  <title>menu</title>
                  <desc>Created with Sketch.</desc>
                  <g id="Homepage" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                      <g id="Home-sm" transform="translate(-335.000000, -62.000000)" fill="#242845">
                          <g id="menu" transform="translate(335.000000, 62.000000)">
                              <rect id="Rectangle" x="0" y="0" width="25" height="2" rx="1"></rect>
                              <rect id="Rectangle-Copy-10" x="0" y="8" width="25" height="2" rx="1"></rect>
                              <rect id="Rectangle-Copy-11" x="0" y="16" width="25" height="2" rx="1"></rect>
                          </g>
                      </g>
                  </g>
              </svg>
            </button>
            <div className="collapse navbar-collapse" id="navbarNavDropdown">
              <ul className="navbar-nav ml-auto">
                <li className="nav-item">
                  <Link className="nav-link" to="/">
                    Our Model
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/">
                    Our Team
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/">
                    Careers
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/">
                    Giving Back
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/">
                    Contact
                  </Link>
                </li>
                <li className="nav-item">
                  <div className="lets-talk-container">
                    <Link to="/" >Lets Talk</Link>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </nav>
        <nav className={isShowRightNav ? 'menu menu-right right-open': 'menu menu-right'}>
          <Link to="/">Home</Link>
          <Link to="#">Our Model</Link>
          <Link to="#">Our Team</Link>
          <Link to="#">Careers</Link>
          <Link to="#">Contact</Link>
          <div className="lets-talk-button-container">
            <button className="lets-talk-button">Let's Talk</button>
          </div>
        </nav>
      </div>
    );
  }

  return (
    <div>
      <h1>None</h1>
    </div>
  );
};

export default Navbar;
